* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}

.app-wrapper {
	display: grid;
	grid-template-areas: 
		'h h'
		'n c';
	/*grid-template-rows: 60px 1fr;*/
	grid-template-columns: 2fr 10fr;
	grid-gap: 10px;
	width: 1200px;
	margin: 0 auto;
	padding: 20px 0;
}

.app-wrapper-content {
	grid-area: c;
	background-color: cornflowerblue;
}